//controls => login , register
//connected to redux store
/**
 * @todo
 */
import React, { Component } from "react";
import { connect } from "react-redux";
import { login, logout, register } from "../../../redux/actions";
import HomeCard from "./HomeCard";
import RegisterModal from "./auth/RegisterModal";
import LoginModal from "./auth/LoginModal";
import $ from "jquery";
import { Link } from "react-router-dom";
class Home extends Component {
  state = {
    cardImg: [
      "images/card1.svg",
      "images/card2.svg",
      "images/card3.svg",
      "images/card4.svg"
    ],
    cardHeader: ["Analysis", "Secure", "be connected", "easy to use"]
  };
  //lifecycle hooks
  componentDidMount() {}

  //methods

  _login = (email, password) => {
    //valid
    this.props.login(email, password);
    $("#loginModal").modal("hide");
  };
  _register = (name, email, password, type) => {
    //valid
    this.props.register(name, email, password, type);
    $("#registerModal").modal("hide");
  };
  render() {
    return (
      <div className="home">
        <RegisterModal _register={this._register} />
        <LoginModal _login={this._login} />
        <div className="container">
          <div className="getStarted">
            <div className="getStarted__about">
              <h2>About WorkEgypt</h2>
              <p>
                WorkEgypt.net is created and managed byWorkEgypt, a recruitment
                and training consultancy company specialized in designing
                Innovative Online Recruitment Solutions and recruitment and
                training consultancy We're helping employers and job seekers to
                find their right match through our intelligent real-time
                recommendations and around the clock support.
              </p>
              <Link to="/jobs" className="general-btn">
                Get Started
              </Link>
            </div>

            <div className="getStarted__images-container">
              <img
                className="getStarted__curve"
                src="images/Path_3.svg"
                alt=""
              />
              <img
                className="getStarted__img"
                src="images/Home-1_0.svg"
                alt=""
              />
            </div>
          </div>
        </div>
        <div className="container">
          <div className="applicant-benefits row">
            <div className="applicant-benefits__text col-lg-6">
              <h2>Looking for a job?</h2>
              <p className="">
                If you are searching for a new career opportunity, you
                cansearch open vacancies and jobs. You can alsosignup hereto
                be alerted of new jobs by email.
              </p>
            </div>
            <div className="applicant-benefits__img col-lg-6">
              <img src="images/Group.svg" alt="" />
            </div>
          </div>
        </div>
        <div className="companies-benefits">
          <img
            className="companies-benefits__img"
            src="images/dsds.svg"
            alt=""
          />
          <div className="companies-benefits__text ">
            <h3>Are you a recruiter or employer?</h3>
            <p className="">
              If you are currently hiring, and would like to advertise your jobs
              on WorkEgypt .net, pleasesignup for an employer accountand post
              your jobs right away.
            </p>
          </div>
        </div>
        <div className="container">
          <div className="row">
            <HomeCard
              cardImg={this.state.cardImg[0]}
              cardHeader={this.state.cardHeader[0]}
            />
            <HomeCard
              cardImg={this.state.cardImg[1]}
              cardHeader={this.state.cardHeader[1]}
            />
            <HomeCard
              cardImg={this.state.cardImg[2]}
              cardHeader={this.state.cardHeader[2]}
            />
            <HomeCard
              cardImg={this.state.cardImg[3]}
              cardHeader={this.state.cardHeader[3]}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default connect(null, { login, logout, register })(Home);
